import { Router } from 'express';
import AdminAnalyticsController from './analytics.controller';
import AdminAnalyticsService from './analytics.service';
import { IData } from '@/utils/types/types';

export const createAnalyticsRouter = (data: IData): Router => {
  const router = Router();
  const service = new AdminAnalyticsService(data.model);
  const controller = new AdminAnalyticsController(service);

  // GET routes
  router.get('/', controller.getAnalytics);
  router.get('/latest', controller.getLatestAnalytics);
  router.get('/date-range', controller.getDateRangeAnalytics);
  router.get('/dashboard', controller.getDashboard);
  router.get('/today', controller.getTodayAnalytics);
  router.get('/weekly', controller.getWeeklyAnalytics);
  router.get('/monthly', controller.getMonthlyAnalytics);
  
  // Specific metrics routes
  router.get('/merchants', controller.getMerchantMetrics);
  router.get('/transactions', controller.getTransactionMetrics);
  router.get('/sdk', controller.getSDKMetrics);

  // POST routes
  router.post('/refresh', controller.refreshAnalytics);

  return router;
};

export default createAnalyticsRouter;

