import { Router } from "express";
import SubscriberController from "./subscriber.controller";
import SubscriberService from "./subscriber.service";
import { GetSubscriberDTO, GetSubscribersDTO } from "./subscriber.type";
import { IData } from "@/utils/types/types";
import { validateParams, validateQuery } from "../../../../shared/adapter/middleware/validate";
import { MerchantRole } from "../../../../shared/adapter/middleware/roles/merchant.role";

const subscriberRouter = (data: IData): Router => {
    const router = Router();
    const subscriberController = new SubscriberController(new SubscriberService(data.model));

    router.get('/:id', MerchantRole(data), validateParams(GetSubscriberDTO), subscriberController.getSubscriber);
    router.get('/', MerchantRole(data), validateQuery(GetSubscribersDTO), subscriberController.getSubscribers);

    return router
}

export default subscriberRouter;