
import { IsArray, IsDate, IsIn, IsNumber, IsOptional, IsString } from 'class-validator';
import { Transform } from 'class-transformer';
import { IBlockchainType } from '@/models/wallet/type';
// import { IPaginateOptions } from '@/utils/types/types';

export type IGetSubscriber = {
    id: string;
};

export class GetSubscriberDTO implements IGetSubscriber {
  @IsString({ message: 'ID must be a string' })
  @Transform(({ value }) => value?.trim())
  id!: string;
}

export type IGetSubscribers = {
  account_id: string;
  page: string;
  limit: string;
  amount_min?: string;
  amount_max?: string;
  from?: Date;
  to?: Date;
};

export class GetSubscribersDTO implements IGetSubscribers {
  @IsString({ message: 'ID must be a string' })
  @Transform(({ value }) => value?.trim())
  account_id!: string;

  @Transform(({ value }) => parseInt(value))
  @IsNumber()
  page!: string;

  @Transform(({ value }) => parseInt(value))
  @IsNumber()
  limit!: string;

  @IsOptional()
  @Transform(({ value }) => parseFloat(value))
  @IsNumber({}, { message: 'amount_min must be a number' })
  amount_min?: string;

  @IsOptional()
  @Transform(({ value }) => parseFloat(value))
  @IsNumber({}, { message: 'amount_max must be a number' })
  amount_max?: string;

  @IsOptional()
  @Transform(({ value }) => new Date(value))
  @IsDate({ message: 'from must be a valid date' })
  from?: Date;

  @IsOptional()
  @Transform(({ value }) => new Date(value))
  @IsDate({ message: 'to must be a valid date' })
  to?: Date;
}