import { TelegrafRouter } from "@/libraries/telegram";
import { IData } from "@/utils/types/types";
import transferRouter from "./onboard/onboard.route";

const botRouter = (data: IData) => {
  const router = TelegrafRouter(data.repo.encryption);
  router.extendsRoutes(transferRouter(data));
  return router;
};

export default botRouter;
