import { Telegraf, Context } from "telegraf";
import EncryptionRepository, { Token } from "@/repositories/encryption";

interface MySession {
  token?: string;
}

interface MyContext extends Context {
  session: MySession;
}

// Request object for bot routes
export type IBotRequest<Request> = {
  // auth: IAuthApp;
  message: Request;
  ctx: Context;
};

// Route action type
export type IBotAction<Request> = (
  req: IBotRequest<Request>,

) => void;

type IBotInfo<Request> = {
  command: string;
  action: IBotAction<Request>;
};

export class TelegrafRouterConstructor {
  private encryption: EncryptionRepository;
  botInfo: IBotInfo<any>[] = [];

  constructor(encryption: EncryptionRepository) {
    this.encryption = encryption;
  }

  start = (action: IBotAction<string>) => {
    this.botInfo.push({ command: "start", action });
  };

  route = <Request>(
    command: string,
    action: IBotAction<Request>
  ) => {
    this.botInfo.push({ command, action });
  };

  extendsRoutes = (router: TelegrafRouterConstructor) => {
    this.botInfo = [...this.botInfo, ...router.botInfo];
  };

  runBot = (token: string) => {
    const bot: Telegraf<MyContext> = new Telegraf<MyContext>(token);
    // Register all routes
    this.botInfo.forEach((info) => {
      if (!info.command || !info.action) return;
      if (info.command === "start") {
        bot.start(async (ctx) => {
          try {
            // extract token (you could use ctx.message.text or session data)
            // const token = ctx.session?.token || null; // example: session storage
            // if (!token) {
            //   await ctx.reply(" Authentication required.");
            //   return;
            // }

            // const stringifiedAccount = this.encryption.decryptId(
            //   token,
            //   Token.apiAccessToken as "Special Token"
            // );
            // if (!stringifiedAccount.status) {
            //   await ctx.reply(" Invalid token.");
            //   return;
            // }

            // const account: IAuthApp = JSON.parse(stringifiedAccount.data);
            // if (!account.user_id) {
            //   await ctx.reply(" Authentication failed.");
            //   return;
            // }

            info.action(
              {
                message: "start",
                // auth: {
                //   user_id: account.user_id,
                //   account_id: account.account_id,
                //   account_type: account.account_type,
                // },
                ctx,
              },
            );
          }
          catch (err) {
            console.log("Bot error:", err);
            await ctx.reply(" Something went wrong.");
          }
        });
        return;
      }

      bot.command(info.command, async (ctx) => {
        try {
          // extract token (you could use ctx.message.text or session data)
          // const token = ctx.session?.token || null; // example: session storage
          // if (!token) {
          //   await ctx.reply(" Authentication required.");
          //   return;
          // }

          // const stringifiedAccount = this.encryption.decryptId(
          //   token,
          //   Token.apiAccessToken as "Special Token"
          // );
          // if (!stringifiedAccount.status) {
          //   await ctx.reply(" Invalid token.");
          //   return;
          // }

          // const account: IAuthApp = JSON.parse(stringifiedAccount.data);
          // if (!account.user_id) {
          //   await ctx.reply(" Authentication failed.");
          //   return;
          // }

          // strip command prefix and pass arguments as message
          const args = ctx.message.text.split(" ").slice(1).join(" ");

          info.action(
            {
              message: args as unknown as any, // type-safe if you enforce DTO
              // auth: {
              //   user_id: account.user_id,
              //   account_id: account.account_id,
              //   account_type: account.account_type,
              // },
              ctx,
            },
          );
        } catch (err) {
          console.log("Bot error:", err);
          await ctx.reply(" Something went wrong.");
        }
      });
    });

    bot.launch();
    console.log(" Telegram bot running...");
  }
}

export const TelegrafRouter = (
  encryption: EncryptionRepository
) => {
  return new TelegrafRouterConstructor(encryption);
};
