import { Request, Response } from 'express';
import SubscriberService from './subscriber.service';
import { IGetSubscriber, IGetSubscribers } from './subscriber.type';

class SubscriberController {
  constructor(private readonly subscriberService: SubscriberService) {}

  getSubscriber = async (req: Request<IGetSubscriber>, res: Response) => {
    try {
      const response = await this.subscriberService.getSubscriber(req.userAuth, req.params);
      res.status(response.status ? 200 : 400).json(response);
    } catch (err) {
      res.status(500).json({ status: false, message: 'Internal Server Error' });
    }
  };

  getSubscribers = async (req: Request<{}, {}, {}, any>, res: Response) => {
    try {
      const response = await this.subscriberService.getSubscribers(req.userAuth, req.query as IGetSubscribers);
      res.status(response.status ? 200 : 400).json(response);
    } catch (err) {
      res.status(500).json({ status: false, message: 'Internal Server Error' });
    }
  };
}

export default SubscriberController;