import { Models } from '@/models';
import { IResponse } from '@/utils/types/types';
import { IAdminAnalyticsResponse, IAdminAnalyticsQuery } from '@/models/analytics/admin/types';

class AdminAnalyticsService {
  constructor(private readonly models: Models) {}

  /**
   * Get comprehensive admin analytics
   */
  getAnalytics = async (query: IAdminAnalyticsQuery = {}): Promise<IResponse<IAdminAnalyticsResponse>> => {
    try {
      // Calculate fresh analytics based on the query
      const analytics = await this.models.analytics.admin.calculateAnalytics(this.models, query);

      if (!analytics) {
        return { 
          status: false, 
          message: 'Unable to calculate analytics' 
        };
      }

      return { 
        status: true, 
        data: analytics.toResponse 
      };
    } catch (error) {
      console.error('Error fetching admin analytics:', error);
      return { 
        status: false, 
        message: 'Failed to retrieve analytics' 
      };
    }
  };

  /**
   * Get latest cached analytics for a specific period type
   */
  getLatestAnalytics = async (
    periodType: 'hourly' | 'daily' | 'weekly' | 'monthly' | 'yearly' | 'all-time' = 'daily'
  ): Promise<IResponse<IAdminAnalyticsResponse>> => {
    try {
      const analytics = await this.models.analytics.admin.getLatestAnalytics(periodType);

      if (!analytics) {
        // If no cached analytics found, calculate fresh ones
        return this.getAnalytics({ period_type: periodType });
      }

      return { 
        status: true, 
        data: analytics.toResponse 
      };
    } catch (error) {
      console.error('Error fetching latest analytics:', error);
      return { 
        status: false, 
        message: 'Failed to retrieve latest analytics' 
      };
    }
  };

  /**
   * Get analytics for a specific date range
   */
  getAnalyticsForDateRange = async (
    startDate: Date, 
    endDate: Date
  ): Promise<IResponse<IAdminAnalyticsResponse[]>> => {
    try {
      const analyticsList = await this.models.analytics.admin.getAnalyticsForDateRange(
        startDate, 
        endDate
      );

      if (!analyticsList || analyticsList.length === 0) {
        return { 
          status: false, 
          message: 'No analytics found for the specified date range' 
        };
      }

      return { 
        status: true, 
        data: analyticsList.map(a => a.toResponse) 
      };
    } catch (error) {
      console.error('Error fetching analytics for date range:', error);
      return { 
        status: false, 
        message: 'Failed to retrieve analytics for date range' 
      };
    }
  };

  /**
   * Get dashboard overview (all-time analytics)
   */
  getDashboardOverview = async (): Promise<IResponse<IAdminAnalyticsResponse>> => {
    try {
      return this.getAnalytics({ period_type: 'all-time' });
    } catch (error) {
      console.error('Error fetching dashboard overview:', error);
      return { 
        status: false, 
        message: 'Failed to retrieve dashboard overview' 
      };
    }
  };

  /**
   * Get today's analytics
   */
  getTodayAnalytics = async (): Promise<IResponse<IAdminAnalyticsResponse>> => {
    try {
      return this.getAnalytics({ 
        period_type: 'daily',
        start_date: new Date(),
        end_date: new Date()
      });
    } catch (error) {
      console.error('Error fetching today\'s analytics:', error);
      return { 
        status: false, 
        message: 'Failed to retrieve today\'s analytics' 
      };
    }
  };

  /**
   * Get this week's analytics
   */
  getWeeklyAnalytics = async (): Promise<IResponse<IAdminAnalyticsResponse>> => {
    try {
      return this.getAnalytics({ period_type: 'weekly' });
    } catch (error) {
      console.error('Error fetching weekly analytics:', error);
      return { 
        status: false, 
        message: 'Failed to retrieve weekly analytics' 
      };
    }
  };

  /**
   * Get this month's analytics
   */
  getMonthlyAnalytics = async (): Promise<IResponse<IAdminAnalyticsResponse>> => {
    try {
      return this.getAnalytics({ period_type: 'monthly' });
    } catch (error) {
      console.error('Error fetching monthly analytics:', error);
      return { 
        status: false, 
        message: 'Failed to retrieve monthly analytics' 
      };
    }
  };

  /**
   * Get merchant-specific metrics
   */
  getMerchantMetrics = async (query: IAdminAnalyticsQuery = {}): Promise<IResponse<any>> => {
    try {
      const analytics = await this.models.analytics.admin.calculateAnalytics(this.models, query);

      if (!analytics) {
        return { 
          status: false, 
          message: 'Unable to calculate merchant metrics' 
        };
      }

      return { 
        status: true, 
        data: analytics.merchant_metrics 
      };
    } catch (error) {
      console.error('Error fetching merchant metrics:', error);
      return { 
        status: false, 
        message: 'Failed to retrieve merchant metrics' 
      };
    }
  };

  /**
   * Get transaction-specific metrics
   */
  getTransactionMetrics = async (query: IAdminAnalyticsQuery = {}): Promise<IResponse<any>> => {
    try {
      const analytics = await this.models.analytics.admin.calculateAnalytics(this.models, query);

      if (!analytics) {
        return { 
          status: false, 
          message: 'Unable to calculate transaction metrics' 
        };
      }

      return { 
        status: true, 
        data: analytics.transaction_metrics 
      };
    } catch (error) {
      console.error('Error fetching transaction metrics:', error);
      return { 
        status: false, 
        message: 'Failed to retrieve transaction metrics' 
      };
    }
  };

  /**
   * Get SDK-specific metrics
   */
  getSDKMetrics = async (query: IAdminAnalyticsQuery = {}): Promise<IResponse<any>> => {
    try {
      const analytics = await this.models.analytics.admin.calculateAnalytics(this.models, query);

      if (!analytics) {
        return { 
          status: false, 
          message: 'Unable to calculate SDK metrics' 
        };
      }

      return { 
        status: true, 
        data: analytics.sdk_metrics 
      };
    } catch (error) {
      console.error('Error fetching SDK metrics:', error);
      return { 
        status: false, 
        message: 'Failed to retrieve SDK metrics' 
      };
    }
  };

  /**
   * Refresh analytics (force recalculation)
   */
  refreshAnalytics = async (query: IAdminAnalyticsQuery = {}): Promise<IResponse<IAdminAnalyticsResponse>> => {
    try {
      // Always calculate fresh analytics
      const analytics = await this.models.analytics.admin.calculateAnalytics(this.models, query);

      if (!analytics) {
        return { 
          status: false, 
          message: 'Unable to refresh analytics' 
        };
      }

      return { 
        status: true, 
        message: 'Analytics refreshed successfully',
        data: analytics.toResponse 
      };
    } catch (error) {
      console.error('Error refreshing analytics:', error);
      return { 
        status: false, 
        message: 'Failed to refresh analytics' 
      };
    }
  };
}

export default AdminAnalyticsService;

