import { IData } from "@/utils/types/types";
import OnboardController from "./onboard.controller";
import OnboardService from "./onboard.service";
import { TelegrafRouter } from "@/libraries/telegram";

const transferRouter = (data: IData) => {
  const router = TelegrafRouter(data.repo.encryption);
  const controller = new OnboardController(new OnboardService(data.model, data.repo));

  router.start(controller.welcome);

  return router;
};

export default transferRouter;
