import { Router } from "express";
import { IData } from "@/utils/types/types";
import authRouter from "./auth/auth.routes";
import { createAnalyticsRouter } from "./analytics/analytics.routes";

const adminRouter = (data: IData): Router => {
    const router = Router();

    router.use('/auth', authRouter(data));
    router.use('/analytics', createAnalyticsRouter(data));

    return router;
}

export default adminRouter;