import { IBotRequest } from "@/libraries/telegram";
import TransferService from "./onboard.service";
import { Context, Markup } from "telegraf";

class TransferController {
  constructor(private readonly transferService: TransferService) {}

  welcome = async ({ ctx }: IBotRequest<string>) => {
    const response = await this.transferService.welcome(ctx.from!);
    if (!response.status) {
      await ctx.reply(" An error occurred. Please try again later.");
      return;
    }
    await ctx.reply(response.data.title, {
      parse_mode: "Markdown",
      ...Markup.inlineKeyboard([
        [Markup.button.callback(" Lay Complaints", "user_complaints")],
        [Markup.button.callback(" Settings", "settings")],
      ]),
    });
  };

  userComplaints = async ({ ctx }: IBotRequest<string>) => {
    await ctx.editMessageText(` *User Complaints*`, {
      parse_mode: "Markdown",
      ...Markup.inlineKeyboard([
        [Markup.button.callback(" Transactions", "transactions")],
        [Markup.button.callback(" Complaints", "complaints")],
        [Markup.button.callback(" Back", "back_home")],
      ]),
    });
  };

  // getTransactionHistory = async ({ ctx }: IBotRequest<string>) => {
  //   const response = await this.transferService.getTransactionHistory(ctx.from!);
  //   await ctx.editMessageText(` *Transaction History*`, {
  //     parse_mode: "Markdown",
  //     ...Markup.inlineKeyboard([
  //       [Markup.button.callback(" Back", "back_home")],
  //     ]),
  //   });
  // };

  backHome = async ({ ctx }: IBotRequest<string>) => {
    await ctx.editMessageText(" *Main Menu*", {
      parse_mode: "Markdown",
      ...Markup.inlineKeyboard([
        [Markup.button.callback(" Start Transfer", "start_transfer")],
        [Markup.button.callback(" Receive Payment", "receive_payment")],
        [Markup.button.callback(" Settings", "settings")],
      ]),
    });
  };
}

export default TransferController;
