
import { Models } from "@/models";
import { IAuthUser, IPaginateResponse, IResponse } from "@/utils/types/types";
import { IGetSubscriber, IGetSubscribers } from "./subscriber.type";
import { userLogger } from "../../../../shared/adapter/libraries/logger";
import { Between, DeepPartial, FindOptionsWhere, In, LessThan, MoreThan } from "typeorm";
import { IGeneralUserWallet, IUserWallet } from "@/models/user/types";
import { UserWallet } from "@/models/user";

class SubscriberService {
  constructor(private readonly models: Models) {}

  getSubscriber = async (auth: IAuthUser, request: IGetSubscriber): Promise<IResponse<IGeneralUserWallet>> => {
    try {
      const merchant = await this.models.user_wallet.findOne({
        where: { id: request.id, account: { merchant_id: auth.id } },
        relations: { user: true, wallet: true },
      });
      if (!merchant) return { status: false, message: "unable to get user" };

      return { status: true, data: merchant.toSecureResponse };
    } catch (error) {
      console.error("Error fetching subscriber:", error);
      return { status: false, message: "unable to get user" };
    }
  };

  getSubscribers = async (auth: IAuthUser, request: IGetSubscribers): Promise<IResponse<IPaginateResponse<IGeneralUserWallet>>> => {
    try {
      const filters: FindOptionsWhere<UserWallet> = {
        account: { 
          id: request.account_id,
          merchant: { id: auth.id },
        }
      };
  
      if (request.from || request.to) {
        if (request.from && request.to) {
          filters.created_at = Between(request.from, request.to);
        } else if (request.from) {
          filters.created_at = MoreThan(request.from);
        } else if (request.to) {
          filters.created_at = LessThan(request.to);
        }
      }
  
      const subscribers = await this.models.user_wallet.paginate({
        where: filters,
        options: { limit: parseInt(request.limit), page: parseInt(request.page) }
      });
  
      if (!subscribers) return { status: false, message: "unable to get subscribers" };
  
      return {
        status: true,
        data: subscribers.map((subscriber) => subscriber.toSecureResponse),
      };
    } catch (error) {
      userLogger.error("Error fetching subscribers:", error);
      return { status: false, message: "unable to get subscribers" };
    }
  };
  
}

export default SubscriberService;
